.App {

}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

header {
  height: 100px;
  background-color: #282c34;
}
.container-menu {
  display: flex;
  justify-content: center;
}

menu {
  display: flex;
  justify-content: center;
}
.rover-image {
  width: 100px;
  height: 100px;
  border-radius: 3px;
}

.content-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
